<template>
  <!-- eslint-disable -->
  <b-sidebar
    id="add-sede"
    :visible="isAdd"
    bg-variant="white"
    sidebar-class="sidebar-lg"
    shadow
    backdrop
    no-header
    right
    no-close-on-backdrop
    @change="(val) => $emit('update:is-add', val)"
  >
    <template #default="{ hide }">
      <!-- Header -->
      <div class="d-flex justify-content-between align-items-center content-sidebar-header px-2 py-1">
        <h5 class="mb-0">
          {{ isViewMode ? 'Visualizar' : isEdit ? 'Editar' : 'Agregar nuevo' }} observacin
        </h5>
        <feather-icon
          class="ml-1 cursor-pointer"
          icon="XIcon"
          size="16"
          @click=";[hide(), resetForm()]"
        />
      </div>

      <!-- BODY -->
      <validation-observer #default="{ invalid }" ref="refFormObserver">
        <b-form class="p-2" @submit.prevent="onSubmit(items)">
          <!-- Proyecto -->
          <validation-provider #default="{ errors }" name="proyecto" >
            <b-form-group label="Proyecto" label-for="proyecto">
              <v-select
                v-model="items.proyecto"
                :options="proyectos"
                label="name"  
                :reduce="proyecto => proyecto.id"  
                placeholder="Seleccione un proyecto"
                :loading="loadingProyectos"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- Nombre y Apellidos -->
          <validation-provider #default="{ errors }" name="nombre" >
            <b-form-group label="Nombre y Apellidos" label-for="nombre">
              <b-form-input
                v-model="items.nombre"
                id="nombre"
                placeholder="Nombre completo"
                autocomplete="off"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- DNI -->
          <validation-provider #default="{ errors }" name="dni" >
            <b-form-group label="DNI" label-for="dni">
              <b-form-input
                v-model="items.dni"
                id="dni"
                placeholder="Nmero de DNI"
                autocomplete="off"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- Fecha -->
          <validation-provider #default="{ errors }" name="fecha" >
            <b-form-group label="Fecha" label-for="fecha">
              <flat-pickr
                v-model="items.fecha"
                class="form-control"
                :config="config"
                placeholder="Seleccione fecha"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- rea -->
          <validation-provider #default="{ errors }" name="area" >
            <b-form-group label="rea" label-for="area">
              <b-form-input
                v-model="items.area"
                id="area"
                placeholder="rea de trabajo"
                autocomplete="off"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- Estado (Seguro/Inseguro) -->
          <validation-provider #default="{ errors }" name="estado" >
            <b-form-group label="Estado" label-for="estado">
              <b-form-radio-group
                v-model="items.estado"
                :options="estadoOptions"
                name="estado"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- Categoras -->
          <validation-provider #default="{ errors }" name="categorias" >
            <b-form-group label="Categoras" label-for="categorias">
              <v-select
                v-model="items.categorias"
                :options="categoriasCompletas"
                label="name"
                :reduce="cat => cat.id"  
                placeholder="Seleccione categoras"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- Riesgos -->
          <validation-provider #default="{ errors }" name="riesgos" >
            <b-form-group label="Riesgos" label-for="riesgos">
              <v-select
                v-model="items.riesgos"
                :options="riesgoOptionsWithAll"
                label="name"
                :reduce="riesgo => riesgo"
                multiple
                placeholder="Seleccione riesgos"
                :loading="loadingRiesgos"
                :selectable="option => !items.riesgos.some(r => r.id === 0) || option.id === 0"
                @input="handleRiesgosSelection"
                :disabled="isViewMode"
                :class="{ 'view-mode-select': isViewMode }"
              />
              <small v-if="items.riesgos.some(r => r.id === 0)" class="text-info d-block mt-1">
                <feather-icon icon="InfoIcon" size="14"/> Todos los riesgos estn seleccionados
              </small>
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>



          <!-- Descripcin de la observacin -->
          <validation-provider #default="{ errors }" name="descripcion" >
            <b-form-group label="Descripcin de la observacin" label-for="descripcion">
              <b-form-textarea
                v-model="items.descripcion"
                id="descripcion"
                placeholder="Describa la observacin"
                rows="3"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- Medidas correctivas -->
          <validation-provider #default="{ errors }" name="medidas" >
            <b-form-group label="Medidas Correctivas" label-for="medidas">
              <b-form-textarea
                v-model="items.medidas"
                id="medidas"
                placeholder="Describa las medidas correctivas"
                rows="3"
                :disabled="isViewMode"
              />
              <small class="text-danger">{{ errors[0] }}</small>
            </b-form-group>
          </validation-provider>

          <!-- Form Actions -->
          <div class="d-flex mt-4 justify-content-end" v-if="!isViewMode">
            <b-button
              :disabled="invalid || isViewMode"
              variant="primary"
              class="mr-2"
              type="submit"
            >
              <b-spinner v-if="isDisabled" small />
              <span v-if="isDisabled">Guardando...</span>
              <span v-else>{{ isEdit ? 'Actualizar' : 'Agregar' }}</span>
            </b-button>
            <b-button
              type="button"
              variant="outline-secondary"
              @click=";[hide(), resetForm()]"
            >
              Cancelar
            </b-button>
          </div>
        </b-form>
      </validation-observer>
    </template>
  </b-sidebar>
</template>

<script>
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import vSelect from 'vue-select'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import SedeService from '@/services/SedeService'
import RegisterService from '@/services/RegisterService'
import GroupService from '@/services/GroupService' 

export default {
  components: {
    ValidationProvider,
    ValidationObserver,
    vSelect,
    flatPickr
  },
  props: {
    isAdd: {
      type: Boolean,
      required: true
    },
    isViewMode: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      proyectosList: [], 
      isEdit: false,
      isDisabled: false,
      config: {
        dateFormat: 'd/m/Y',
        locale: {
          firstDayOfWeek: 1,
          weekdays: {
            shorthand: ['Do', 'Lu', 'Ma', 'Mi', 'Ju', 'Vi', 'Sa'],
            longhand: ['Domingo', 'Lunes', 'Martes', 'Mircoles', 'Jueves', 'Viernes', 'Sbado']
          },
          months: {
            shorthand: ['Ene', 'Feb', 'Mar', 'Abr', 'May', 'Jun', 'Jul', 'Ago', 'Sep', 'Oct', 'Nov', 'Dic'],
            longhand: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre']
          }
        }
      },
      estadoOptions: [
        { text: 'Seguro', value: 'seguro' },
        { text: 'Inseguro', value: 'inseguro' }
      ],
      categoriaOptions: [
        'Medio Ambiente',
        'Seguridad',
        'Salud',
        'Calidad'
      ],
      categoriasCompletas: [], 
      categoriaOptions: [],    
      proyectos: [], 
      loadingProyectos: false, 
      riesgoOptions: [], 
      loadingRiesgos: false,
      items: {
        proyecto: '',
        nombre: '',
        dni: '',
        fecha: new Date(),
        area: '',
        estado: 'seguro',
        categorias: [],
        riesgos: [],
        puntos: [],
        descripcion: '',
        medidas: ''
      }
    }
  },
  mounted() {
    this.cargarProyectos();
    this.cargarRiesgos();
    this.cargarCategorias();
  },
  computed: {
    riesgoOptionsWithAll() {
      // Asegurarse de que riesgoOptions est definido
      if (!this.riesgoOptions) return [];

      // Crear opcin "Todos" con misma estructura que otros riesgos
      const todosOption = {id: 0, name: 'Todos'};

      // Asegurarse de que cada riesgo tenga la estructura correcta
      const riesgos = this.riesgoOptions.map(item => ({
        id: item.id,
        name: item.name
      }));

      return [todosOption, ...riesgos];
    },
    proyectos1() {
      return this.proyectosList || []; // Asegrate que projects es el nombre correcto
    }
  },
  methods: {
    parseDateToISO1(dateStr) {
      // Asume formato DD/MM/YYYY
      const [day, month, year] = dateStr.split('/');
      return new Date(`${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}T00:00:00`).toISOString();
    },
    async cargarCategorias() {
      try {
        const response = await GroupService.getGroups('', this.$store)

        if (response && response.data && response.data.rows) {
          // Mapea las categoras al formato que necesitas (array de strings)
          this.categoriaOptions = response.data.rows.map(cat => cat.name)

          // Si necesitas tambin los IDs para el formulario, puedes guardarlos en otro array
          this.categoriasCompletas = response.data.rows // Esto guarda objetos con id y name
        } else {
          console.error('Error al cargar categoras:', response?.message || 'Respuesta invlida')
        }
      } catch (error) {
        console.error('Error al cargar categoras:', error)
      }
    },
    async cargarRiesgos() {
      this.loadingRiesgos = true;
      try {
        const response = await RegisterService.getRisk('', this.$store);
        console.log('Respuesta de riesgos:', response); // Esto muestra que response.data.rows existe

        // Corregimos la validacin de la respuesta
        if (response && response.data && response.data.rows) {
          this.riesgoOptions = response.data.rows;
          console.log('Riesgos cargados:', this.riesgoOptions); // Verificamos los datos cargados
        } else {
          console.error('Error: La estructura de la respuesta no es la esperada', response);
        }
      } catch (error) {
        console.error('Error al cargar riesgos:', error);
      } finally {
        this.loadingRiesgos = false;
      }
    },
    getProjects() {
      return this.proyectos // Retorna el array de proyectos que ya cargas
    },
    async cargarProyectos() {
      this.loadingProyectos = true;
      try {
        const response = await SedeService.getProyectos('', this.$store);
        if (response.status) {
          this.proyectos = response.data.rows;
        } else {
          console.error('Error al cargar proyectos:', response.data.message);
        }
      } catch (error) {
        console.error('Error al cargar proyectos:', error);
      } finally {
        this.loadingProyectos = false;
      }
    },
    handleRiesgosSelection(selected) {
      // Verificar si se est seleccionando "Todos" (id: 0)
      const isSelectingAll = selected.some(item => item.id === 0);

      // Verificar si ya tena "Todos" seleccionado
      const hadAllSelected = this.items.riesgos.some(item => item.id === 0);

      // Si se selecciona "Todos" (ya sea que antes tena o no)
      if (isSelectingAll) {
        // Limpiar toda seleccin anterior y dejar solo "Todos"
        this.items.riesgos = selected.filter(item => item.id === 0);
        return;
      }
    
      // Si estaba seleccionado "Todos" y ahora se quita
      if (hadAllSelected && !isSelectingAll) {
        // Permitir la nueva seleccin (sin "Todos")
        this.items.riesgos = selected;
        return;
      }
    
      // En cualquier otro caso, actualizar la seleccin normalmente
      this.items.riesgos = selected;
    },
    setData(item) {
      return new Promise((resolve) => {
        if (item) {
          this.items = {
            ...item,
            riesgos: Array.isArray(item.riesgos) ? item.riesgos : [],
            puntos: Array.isArray(item.puntos) ? item.puntos : []
          };
          this.isEdit = !item.isViewMode; // Solo ser true si no es modo visualizacin
          this.isViewMode = item.isViewMode || false; // Establece el modo visualizacin
        } else {
          this.resetForm();
          this.isEdit = false;
          this.isViewMode = false;
        }

        // Resolver la promesa despus de que Vue haya actualizado la vista
        this.$nextTick(() => {
          resolve();
        });
      });
    },
    resetForm() {
      this.items = {
        proyecto: '',
        nombre: '',
        dni: '',
        fecha: new Date(),
        area: '',
        estado: 'seguro',
        categorias: [],
        riesgos: [],
        puntos: [],
        descripcion: '',
        medidas: ''
      }
      if (this.$refs.refFormObserver) {
        this.$refs.refFormObserver.reset()
      }
    },
    
    async onSubmit() {
      this.isDisabled = true;
        
      try {
        
        // Preparar los datos para enviar en el formato que espera tu API
        const formData = {
          projectId: this.items.proyecto,
          worker_fullname: this.items.nombre,
          worker_id_number: this.items.dni,
          completed: this.items.fecha ? this.parseDateToISO1(this.items.fecha) : null,
          area: this.items.area,
          flag: this.items.estado === 'seguro' ? 1 : 0,
          categoryId: Number(this.items.categorias),
          description: this.items.descripcion,
          actions: this.items.medidas,
          risks: this.items.riesgos
          ? this.items.riesgos
              .filter(r => r.id !== 0) // Filtra "Todos" si existe
              .map(r => ({ id: r.id }))
          : []
        };
      
        let response;
      
        if (this.isEdit) {
          // Actualizar registro existente
          response = await RegisterService.updateRecord(this.items.id, formData, this.$store);
        }
      
        if (response && response.status !== false) {
          // Emitir evento para actualizar la lista
          this.$parent.$parent.getAllData()
          this.$emit('update:is-add', false)
          this.resetForm();
        } else {
          throw new Error(response?.message || 'Error al actualizar el registro');
        }
      } catch (error) {
        console.error('Error al guardar registro:', error);
      } finally {
        this.isDisabled = false;
      }
    }
  }
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
@import '@core/scss/vue/libs/vue-flatpicker.scss';

.content-sidebar-header {
  border-bottom: 1px solid #ebe9f1;
}

.form-group {
  margin-bottom: 1.5rem;
}

.text-danger {
  font-size: 0.857rem;
}
/* Estilos para el v-select en modo visualizacin */
.view-mode-select {
  &.vs--disabled {
    .vs__dropdown-toggle {
      background-color: white !important;
      border-color: #d8d6de !important;
      cursor: default !important;
    }
    
    .vs__selected {
      color: #6e6b7b !important;
      background-color: #f8f8f8 !important;
      border-color: #d8d6de !important;
    }
    
    .vs__search, 
    .vs__clear, 
    .vs__open-indicator {
      display: none !important;
    }
    
    .vs__actions {
      display: none !important;
    }
  }
}

/* Estilos para otros campos deshabilitados */
.form-control:disabled,
.form-control[readonly] {
  background-color: white !important;
  color: #6e6b7b !important;
  border-color: #d8d6de !important;
  cursor: default !important;
  opacity: 1 !important;
}

/* Estilos para radio buttons deshabilitados */
.custom-control-input:disabled ~ .custom-control-label {
  color: #6e6b7b !important;
  cursor: default !important;
}

/* Estilos para textareas deshabilitadas */
textarea:disabled {
  background-color: white !important;
  color: #6e6b7b !important;
  border-color: #d8d6de !important;
  cursor: default !important;
  resize: none !important;
}
/* Estilos para flatpickr deshabilitado */
.flatpickr-input[readonly] {
  background-color: white !important;
  cursor: default !important;
}
.view-mode-select {
  &.vs--disabled {
    .vs__selected {
      padding: 0.25rem 0.5rem;
      background-color: #005193 !important;
      border-color: #005193 !important;
      color: white !important;
      border-radius: 5px;
      margin: 2px;
      display: inline-flex;
    }
    
    .vs__deselect {
      display: none;
    }
  }
}
</style>